//ngx-bootstrap
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { ModalModule } from 'ngx-bootstrap/modal';
//ngx-bootstrap
import { ClipboardModule } from 'ngx-clipboard';
import { NgDatepickerModule } from 'ng2-datepicker';


import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { EssentialHeaderComponent } from './essential-header/essential-header.component';
import { EssentialComponent } from './essential/essential.component';
import { EssentialNavComponent } from './essential-nav/essential-nav.component';

//material
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatDividerModule, MatListModule, MatGridListModule, MatCardModule, MatMenuModule, MatIconModule, MatButtonModule } from '@angular/material';
import { WelcomeComponent } from './welcome/welcome.component';
import { LayoutModule } from '@angular/cdk/layout';

@NgModule({
  declarations: [
    AppComponent,
    EssentialHeaderComponent,
    EssentialComponent,
    EssentialNavComponent,
    WelcomeComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ClipboardModule,
    NgDatepickerModule,
    BsDropdownModule.forRoot(),
    TooltipModule.forRoot(),
    ModalModule.forRoot(),
    BrowserAnimationsModule,
    MatDividerModule, MatListModule, MatGridListModule, MatCardModule, MatMenuModule, MatIconModule, MatButtonModule, LayoutModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
