import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'essential-nav',
  templateUrl: './essential-nav.component.html',
  styleUrls: ['./essential-nav.component.css']
})
export class EssentialNavComponent implements OnInit {
  icon: string;
  style: any;

  @Input('icon') input_icon: string;

  @Input('style') set value(val: any) {
    this.style = val;
  }
  
  ngOnInit() {
    this.icon = this.input_icon;
  }
}
