import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

import { Component, OnInit } from '@angular/core';
import { pouchService } from '../services/pouchService';
import { TemplateRef } from '@angular/core';
import { pouchServiceV2 } from '../services/pouchService-2.0';
@Component({
  selector: 'essential',
  templateUrl: './essential.component.html',
  styleUrls: ['./essential.component.css']
})

export class EssentialComponent implements OnInit {
  //services  
  credentialService: pouchServiceV2;
  cookieService: pouchServiceV2;
  dataService: pouchServiceV2;
  sprintService: pouchServiceV2;
  legacyService: pouchServiceV2;

  //styles
  defaultstyles: any;
  iconstyle: any;
  //Service: pouchServiceV2;
  public modalRef: BsModalRef;
  DocStore: any;
  docs = [];
  dailydata = [];
  credentials = [];
  newDoc: string;
  newCredential = { title: '', username: '', password: '' };
  newSprint = { name: '', start: new Date(), devend: new Date(), testend: new Date(), tickets: [{ name: '', desc: '' }] };
  sprints = [];
  sprint = {
    show: false,
    name: '',
    tickets: [
      { name: '', desc: '' }
    ],
    start: '',
    devend: '',
    testend: ''
  };
  isLink: boolean = false;
  dateOptions = {
    displayFormat: 'D MMM',
    addStyle: { width: '70px', height: '25px' }
  };
  showDelete: boolean = false;
  mode: number = 0;

  constructor(_docStore: pouchService, private modalService: BsModalService) {
    this.initializeServices();
    this.initializeData();
    //this.setDefaultStylesForIcons();
    this.DocStore = _docStore;
  }

  ngOnInit() {
    this.docs = this.DocStore.getDocs();
  }

  tkt = [0];
  addNewTicketInput() {
    this.newSprint.tickets.push({ name: '', desc: '' });
    this.tkt.push(this.tkt.length);
    this.tkt = this.tkt; //for change detection
  }
  saveCredential(credential: any) {
    this.DocStore.saveCredential(credential);
  }

  //-------------
  saveSprint() {
    let context = this;
    context.DocStore.saveSprint(context.newSprint).then((saveStatus) => console.log("isSprintSaved: " + saveStatus));
  }
  //-------------
  save(mode: number): void {
    switch (mode) {
      case 0:
        this.saveDailyDoc(this.newDoc);
        this.resetDocs();
        break;
      case 1:
        this.saveCredential(this.newCredential);
        this.resetCredentials();
        break;
      case 2:
        this.saveSprint();
        this.resetSprint();
        break;
      default:
    }
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template); // {3}
  }

  toggleCredentialDataDivBG(index: number): void {
    this.docs[index].bg = !this.docs[index].bg;
  }

  toggleDailyDataDivBG(index: number): void {
    this.dailydata[index].bg = !this.dailydata[index].bg;
  }

  saveDailyDoc(newDoc: string) {
    newDoc = newDoc.trim();
    if (newDoc != '') {
      if (this.isLink) {
        newDoc = '<a href=' + newDoc.split(',')[0] + '>' + (newDoc.split(',')[1] == undefined ? 'New Link' : newDoc.split(',')[1]) + '</a>';
      }
      this.DocStore.saveDailyDoc(newDoc);
      this.newDoc = '';
    }
    this.newDoc = '';
    this.isLink = false;
  }

  deleteDoc(id: string, rev: string) {
    this.DocStore.deleteDoc(id, rev);
    this.resetDocs();
  }

  deleteDailyDoc(id: string, rev: string) {
    this.DocStore.deleteDailyDoc(id, rev);
    this.resetDocs();
  }
  
  iconToggleColor(index: number, iconname: string, event: string) {
    if (this.iconstyle.freezeCredentialStyleChange && event == 'hover') return;
    this.setNormalStyleToAllIcons();
    switch (iconname) {
      case 'user':
        this.iconstyle.user[index] = this.handleEvent(event);
        window.setTimeout(() => {
          this.iconstyle.user[index] = this.defaultstyles.normal;
        }, 1500)
        break;
      case 'pass':
        this.iconstyle.pass[index] = this.handleEvent(event);
        window.setTimeout(() => {
          this.iconstyle.pass[index] = this.defaultstyles.normal;
        }, 1500)
        break;
      default:
    }
  }

  //#region privates

  private initializeServices(): void {
    this.legacyService = new pouchServiceV2({ name: 'essential', auto_compaction: true });
    this.credentialService = new pouchServiceV2({ name: 'credential', auto_compaction: true });
    this.cookieService = new pouchServiceV2({ name: 'cookie', auto_compaction: true });
    this.dataService = new pouchServiceV2({ name: 'daily_data', auto_compaction: true });
    this.sprintService = new pouchServiceV2({ name: 'sprint', auto_compaction: true });
  }

  private initializeData(): void {
    this.setDailyData();
    this.setLegacyData();
    this.setCredentialData();
    this.setSprintData();
    this.setcookieData();
  }

  //data setters
  private setDailyData(): void {
    this.dataService.getAll().then(response => {
      response.forEach(doc => {
        this.dailydata.push({
          id: doc.id,
          data: doc.doc.data,
          rev: doc.doc._rev,
          bg: false });
      });
    })
  }

  private setLegacyData(): void {
    this.legacyService.getAll().then(response => {
      response.forEach(cx => {
        this.docs.push({
          id: cx.doc.id,
          data: cx.doc.data,
          rev: cx.doc._rev,
          bg: false
        })
      })
    });
  }

  private setCredentialData(): any {
    this.credentialService.getAll().then(response => {
      response.forEach(cx => {
        this.credentials.push({
          _id: cx.doc._id,
          title: cx.doc.title,
          username: cx.doc.username,
          password: cx.doc.password
        })
    this.setDefaultStylesForIcons();
      })
    });
  }

  private setSprintData(): void {
    this.sprintService.getAll({ descending: true }).then(response => {
      response.forEach(cx => {
        this.sprints.push({
          name: cx.doc._id,
          start: cx.doc.start,
          devend: cx.doc.devend,
          testend: cx.doc.testend,
          tickets: cx.doc.tickets,
          _rev: cx.doc._rev
        })
        if (this.sprints[0])
          Object.assign(this.sprint, this.sprints[0], { show: true })
      })
    });
  }

  private setcookieData(): void {

  
  }

  private setDefaultStylesForIcons(): any {
    this.defaultstyles = {};
    this.defaultstyles.normal = { 'fill': '#7f8c8d' };
    this.defaultstyles.clicked = { 'fill': '#27ae60' };
    this.defaultstyles.hover = { 'fill': '#34495e' };
    this.defaultstyles.linkIcon = { 'fill': '#7dadda' };

    this.iconstyle = {};
    this.iconstyle.user = [];
    this.iconstyle.pass = [];
    this.iconstyle.freezeCredentialStyleChange = false;
    this.setNormalStyleToAllIcons();
  }

  private setNormalStyleToAllIcons() {
    for (var j = 0; j < this.credentials.length; j++) {
      this.iconstyle.user[j] = this.defaultstyles.normal;
      this.iconstyle.pass[j] = this.defaultstyles.normal;
    }
  }

  private handleEvent(event: string): any {
    if (event == 'click') {
      this.iconstyle.freezeCredentialStyleChange = true;
      window.setTimeout(() => this.iconstyle.freezeCredentialStyleChange = false, 1500);
      return this.defaultstyles.clicked;
    }
    else { //hover
      return this.defaultstyles.hover;
    }
  }

  //reseters
  private resetDocs() {
    this.dailydata = [];
    this.dailydata = this.DocStore.getDailyDocs();
  }
  private resetCredentials() {
    this.credentials = [];
    this.setCredentialData();
  }
  private resetSprint() {
    this.sprints = [];
    this.setSprintData();
  }
}
