import { Injectable } from '@angular/core';
import PouchDB from 'pouchdb';
@Injectable({ providedIn: 'root' })

export class pouchService {
  db: any;
  controldb: any;
  dailydatadb: any;
  credentialdb: any;
  sprintdb: any;

  constructor() {
    this.loadDatabases();
  }

  //Sprint
  saveSprint(sprint: any): boolean {
    let context = this;

    let sprintDoc = {
      _id: sprint.name,
      start: sprint.start,
      devend: sprint.devend,
      testend: sprint.testend,
      tickets: sprint.tickets
    }
    return context.sprintdb.put(sprintDoc).then(() => true).catch(this.logerror);
  }

  getSprint(sprintid: string): any {
    let context = this;

    return context.sprintdb.get(sprintid)
      .then(
        (sprint) => {
          let _sprint = {
            name: sprint._id,
            devend: sprint.devend,
            testend: sprint.testend,
            tickets: sprint.tickets,
            _rev: sprint._rev
          }; return _sprint
        })
      .catch(
        this.logerror);
  }

  public getSprints(): any {
    let context = this;
    let success = function (success) {
      let result = [];
      success.rows.forEach(doc => {
        doc = doc.doc;
        result.push({ name: doc._id, start: doc.start, devend: doc.devend, testend: doc.testend, tickets:doc.tickets, _rev: doc._rev })
      })

      return result;
    }

    return context.sprintdb.allDocs({ include_docs: true }).then(success).catch(context.logerror);

  }

  //Sprint

  saveCredential(credential: any) {
    let context = this;
    let promise = context.credentialdb.put({ _id: new Date(),  title: credential.title, username: credential.username, password: credential.password }).then().catch();
    return promise;
  }

  getCredentials() {
    let context = this;
    let promise = context.credentialdb.allDocs({ include_docs: true }).then(function success(response) {
      return response.rows;
    })
    return promise;
  }

  getServerNumber() {
    let context = this;
    let promise = context.controldb.get("serverNumber").then(function (response) {
      return response.data;
    }).catch(function () {
      context.controldb.put({ _id: "serverNumber", data: "0" })
    })
    return promise;
  }

  saveDoc(data: string, dataType: string) {
    let context = this;
    if (dataType == 'serverNumber') {

      context.controldb.get('serverNumber', function (err, doc) {
        if (err) { context.controldb.put({ _id: "serverNumber", data: "00" }) }
        context.controldb.put({
          _id: 'serverNumber',
          _rev: doc._rev,
          data: data
        }, function (err, response) {
          if (err) { alert(); return console.log(err); }
        });
      });
    }
    else {
      context.db.put({ _id: new Date().toISOString(), data: data });
    }
  }

  saveDailyDoc(data: string) {
    let context = this;
    context.dailydatadb.put({ _id: new Date().toISOString(), data: data });
  }

  getDocs() {
    let docs = [];
    let context = this;
    context.db.allDocs(
      { include_docs: true },
      function (err, dbresponse) {
        if (!err)
          dbresponse.rows.forEach(doc => {
            docs.push({ id: doc.id, data: doc.doc.data, rev: doc.doc._rev, bg: false });
          });
      });
    return docs;
  }

  getDailyDocs() {
    let dailydocs = [];
    let context = this;
    context.dailydatadb.allDocs(
      { include_docs: true },
      function (err, dbresponse) {
        if (!err)
          dbresponse.rows.forEach(doc => {
            dailydocs.push({ id: doc.id, data: doc.doc.data, rev: doc.doc._rev, bg: false });
          });
      });
    return dailydocs;
  }
  deleteDoc(id: string, rev: string) {
    let context = this;
    context.db.remove(id, rev);
  }
  deleteDailyDoc(id: string, rev: string) {
    let context = this;
    context.dailydatadb.remove(id, rev);
  }

  //Privates
  private loadDatabases(): void {
    let context = this;

    context.db = new PouchDB('essential');
    context.controldb = new PouchDB('essential_control', { auto_compaction: true });
    context.dailydatadb = new PouchDB('daily_data');
    context.credentialdb = new PouchDB('credential', { auto_compaction: true });
    context.sprintdb = new PouchDB('sprint', { auto_compaction: true });
  }
  private logerror(error: any): false {
    console.log(error);
    return false;
  }
}
