import { Injectable } from '@angular/core';
import PouchDB from 'pouchdb';
import { Promise } from 'q';
import { query } from '@angular/core/src/render3/query';

//@Injectable({ providedIn: 'root' })


export class pouchServiceV2 {
  private db: any;

  constructor(private _dbSettings: dbSettings) {
    let This = this;
    //create db instance  
    This.db = new PouchDB(_dbSettings.name, _dbSettings);
  }

  get(id: string): Promise<any> {
    return this.db.get(id).then(response => response).catch(this.logerror);
  }

  getAll(queryoption?: { startkey?: string, endkey?: string, limit?: number, descending?: boolean, include_docs?: boolean }): Promise<any> {
    queryoption = Object.assign({}, { include_docs: true }, queryoption);
    return this.db.allDocs(queryoption).then(response => response.rows).catch(this.logerror);
  }

  update(entity: { _id: string, _rev: string }): Promise<boolean> {
    return this.db.put(entity).then(() => true).catch(this.logerror);
  }

  save(entity: any): Promise<boolean> {
    return this.db.post(entity).then(() => true).catch(this.logerror);
  }

  delete(entity: { id: string, rev: string }): Promise<boolean> {
    return this.db.remove(entity.id, entity.rev).then(() => true).catch(this.logerror);
  }

  //privates
  logerror(err: any): boolean {
    console.log('service error');
    console.log(err);
    return false;
  }
}

export interface dbSettings {
  name: string,
  auto_compaction: boolean,
  adapter?: string,
  revs_limit?: number,
  deterministic_revs?: boolean,
  fetch?: (url: any, opts: any) => any,
  auth?: {
    username: string,
    password: string
  },
  skip_setup?: boolean
}
